<script>
      export let link;

      let linkImg;
      const fallback = "https://appiconmaker.co/home/appicon/testid?size=1024";

      var favi = link.link
            .replace("https://", "")
            .replace("http://", "")
            .replace("www.", "")
            .split("/")[0];
      fetch("http://favicongrabber.com/api/grab/" + favi)
            .then((r) => r.json())
            .then((fi) => {
                  if (fi) {
                        linkImg.src = fi.icons[fi.icons.length - 1].src;
                  } else {
                        linkImg.src = fallback;
                  }
            })
            .catch((e) => (linkImg.src = fallback));
</script>

<style type="text/scss">.quick {
  margin: 0.25em 0.75em; }
  .quick img {
    width: 75px;
    height: 75px;
    object-fit: cover;
    border-radius: 15px; }
  .quick a {
    text-decoration: none;
    color: #fff; }
</style>

<div class="quick">
      <a href={link.link}>
            <div>
                  <img
                        bind:this={linkImg}
                        src=""
                        onerror="this.onerror=null;this.src='https://appiconmaker.co/home/appicon/testid?size=1024';"
                        alt={link.name} />
            </div>
            <span style="font-size:0.75em;color:#ddd"> {link.name} </span>
      </a>
</div>
